import type { Ref } from "vue";
import { defineQueryOptions, useQuery } from "@pinia/colada";
import PosterAPI, { type ILoadPostersOptions } from "~/services/resources/posters";

export const POSTERS_QUERY_KEY = {
  root: ["posters"] as const,
  list: (filters: ILoadPostersOptions) =>
    [...POSTERS_QUERY_KEY.root, "list", {
      platform: filters.platform,
      page: filters.page,
      size: filters.size,
      sort: filters.sort,
      order: filters.order,
    }] as const,
};

export const postersQueryOptions = defineQueryOptions(
  (filters: ILoadPostersOptions) => ({
    key: POSTERS_QUERY_KEY.list(filters),
    query: () => PosterAPI.load(filters),
    staleTime: 1000 * 60 * 15, // 15 minutes
  }),
);

export function usePosters(filters: Ref<ILoadPostersOptions> | (() => ILoadPostersOptions)) {
  return useQuery(postersQueryOptions, filters);
}
