import useRequest from "~/composables/use-request";

export interface ILoadPostersOptions extends IRequestPaginatedParams {
  platform: "mobile" | "web";
}

const PosterAPI = {
  load(options?: ILoadPostersOptions): Promise<IModelPoster[]> {
    const { request } = useRequest();

    const formatPoster = (data: IResponsePoster) => ({
      imageUrl: data.url,
      linkUrl: data.link || data.appLink,
      linkTarget: data.target || data.appTarget,
    });

    const defaultParams = {
      platform: "web",
    };

    const params = Object.assign({}, defaultParams, options);
    return request
      .get<IContentResponse<IResponsePoster[]>>("/api/resources/posters", { query: params })
      .then(response => response.content.map(formatPoster));
  },
};

export default PosterAPI;
